
import { Options, Vue } from "vue-class-component";
import { Logger } from "@/lib/Logger";
import { Plotter, PlotterSteppers } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class StepperMove extends Vue {
  plotter!: Plotter;

  stepperOptions = Object.keys(PlotterSteppers)
    .filter((value) => !isNaN(Number(value)))
    .map((key) => PlotterSteppers[key as keyof typeof PlotterSteppers]);

  stepper: PlotterSteppers = this.stepperOptions[0];

  steps = 0;
  velocity = 0;

  async sendStepper(): Promise<void> {
    this.plotter.sendStepper(this.stepper, this.steps, this.velocity);
  }
}
